import { observer } from 'mobx-react';
import React from 'react';

import { BletherState } from './BletherState';

import './blether.scss';

@observer
export class Blether extends React.PureComponent {
  private readonly bState = new BletherState();
  public render() {
    return (
      <div className={'blether-app'}>
        <div className={'status'}>Status: {this.bState.status} </div>
        <div>Peer id: {this.bState.peerId}</div>
        {this.renderConnectionBox()}
      </div>
    );
  }

  private renderConnectionBox() {
    return (
      <div className={'connect'}>
        <input
          className={'connect-input'}
          type={'text'}
          value={this.bState.connectId}
          onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
            this.bState.setConnectionId(event.target.value)
          }
        />
        <div className={'button'} onClick={() => this.bState.connect()}>
          Connect
        </div>
      </div>
    );
  }
}
